body {
  background-color: #f0f0f0;
  font-family: Arial, Helvetica, sans-serif;
}

.map path {
  stroke-width: .5px;
  stroke: #000;
  pointer-events: fill;
  fill: #fff7dde4;
  background-color: red;
}

.tooltip {
  visibility: hidden;
  color: #fff;
  z-index: 50;
  background-color: #00000080;
  padding: 5px;
  position: absolute;
}

.map path:hover {
  cursor: pointer;
}

